
.mine {
  width: 10rem;
  height: 100%;
  position: relative;
  margin-bottom: 1.5625rem;
}
.mine .set {
  position: absolute;
  width: 0.703125rem;
  height: 0.703125rem;
  top: 0.9375rem;
  right: 0.78125rem;
  z-index: 10000;
}
.mine .set img {
  width: 0.703125rem;
  height: 0.703125rem;
}
.mine .mineHeader {
  width: 10rem;
  height: 9.0625rem;
  background: url('../../assets/images/47/timg.jpg') no-repeat center center;
  background-size: 10rem 9.0625rem;
  background-size: cover;
  -webkit-filter: blur(0.125rem);
          filter: blur(0.125rem);
  z-index: 100;
}
.mine .mineHeader:before {
  content: '';
  position: absolute;
  top: 0;
  width: 100%;
  height: 9.0625rem;
  /*background-color: rgba(0,0,0,0.4);*/
}
.mine .box {
  width: 10rem;
  position: absolute;
  text-align: center;
  top: 0;
  z-index: 1000;
}
.mine .box .center {
  margin-top: 1.71875rem;
  margin-left: 3.625rem;
  width: 2.65625rem;
  height: 2.65625rem;
  border: 5px solid #E4DBD7;
  border-radius: 50%;
  overflow: hidden;
}
.mine .box .center img {
  width: 2.65625rem;
  height: 2.65625rem;
}
.mine .box .content p {
  margin-top: 0.15625rem;
}
.mine .box .content .cont1 {
  font-size: 0.421875rem;
  color: white;
  margin-top: 0.15625rem;
}
.mine .box .content .cont2 {
  font-size: 0.390625rem;
  color: #d8cdc7;
}
.mine .box .content .cont3 {
  font-size: 0.390625rem;
  color: #d8cdc7;
  text-align: left;
  padding: 0 0.9375rem;
  line-height: 0.46875rem;
  margin-top: 0.3125rem;
}
.mine .bottom {
  position: absolute;
  bottom: -2.1875rem;
  height: 1.5625rem;
  width: 10rem;
  /*background:rgba(129,81,65,0.4);*/
  background: rgba(0, 0, 0, 0.2);
}
.mine .bottom div {
  float: left;
  text-align: center;
  font-size: 0.46875rem;
  line-height: 1.5625rem;
  padding-left: 2.078125rem;
  color: #ffffff;
}
.mine .bottom .bottomLeft {
  background: url('../../assets/images/47/profile_erweima_icon.png') no-repeat center 0;
  background-position: 0.9375rem;
  position: relative;
}
.mine .bottom .bottomLeft:after {
  content: '';
  width: 0.03125rem;
  height: 1.25rem;
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0.15625rem;
  right: -0.46875rem;
}
.mine .bottom .bottomRight {
  background: url('../../assets/images/47/profile_edit_icon.png') no-repeat center 0;
  background-position: 0.9375rem;
}
.mine .mineContent {
  height: 2.375rem;
  width: 9.375 rem;
  margin: 0.3125rem;
  border: 1px solid #d7d2e0;
  background: #FAF9F8;
  color: #d1cbdc;
  position: relative;
  border-radius: 0.078125rem;
}
.mine .mineContent > img {
  position: absolute;
  z-index: 1000;
  right: 0.234375rem;
  top: 0.234375rem;
}
.mine .mineContent .mindAdd {
  top: 0.9140625rem;
  right: 0.53125rem;
  z-index: 2000;
}
.mine .mineContent .photo {
  font-size: 0.46875rem;
  line-height: 2.375rem;
  padding-left: 0.25rem;
}
.mine .mineContent .photo img {
  float: left;
  width: 1.875rem;
  height: 1.875rem;
  /*top:15px;
				left:15px;*/
  margin: 15px;
}
.mine .mineBottom {
  margin-bottom: 0.15625rem;
}
.mine .mineBottom .show {
  background: #f2eef9;
}
.mine .mineBottom ul {
  border-top: 1px solid #d7d2e0;
  border-bottom: 1px solid #d7d2e0;
  background: #FFFFFF;
}
.mine .mineBottom ul li {
  height: 1.40625rem;
  line-height: 1.40625rem;
  border-bottom: 1px solid #d7d2e0;
  margin-left: 0.78125rem;
  padding-left: 1.15625rem;
  font-size: 0.5625rem;
  color: #1b1b1b;
  font-family: "微软雅黑";
}
.mine .mineBottom ul li span {
  display: block;
  width: 0.28125rem;
  height: 0.46875rem;
  float: right;
  margin-top: 0.5rem;
  margin-right: 0.5rem;
  background: url('../../assets/images/47/插入箭头.png') no-repeat center center;
}
.mine .mineBottom ul li:last-child {
  border: none;
}
.mine .mineBottom ul li:nth-child(1) {
  background: url('../../assets/images/47/profile_about_icon.png') no-repeat 0 center;
}
.mine .mineBottom ul li:nth-child(2) {
  background: url('../../assets/images/47/opinion.png') no-repeat 0 center;
}
.mine .mineBottom ul li:nth-child(3) {
  background: url('../../assets/images/47/profile_cerify_icon.png') no-repeat 0 center;
}
.mine .authen {
  width: 10rem;
  height: 50%;
  position: absolute;
  bottom: -1.25rem;
  z-index: 10010;
  background: rgba(237, 237, 237, 0.4);
}
.mine .authen .authenContent {
  position: absolute;
  width: 9.375rem;
  margin: 0 0.3125rem;
  padding-bottom: 0.3125rem;
  bottom: 0rem;
  z-index: 10020;
}
.mine .authen .authenContent .autenHead {
  width: 100%;
  height: 3.125rem;
  background: #FFFFFF;
  border-radius: 0.234375rem;
  text-align: center;
}
.mine .authen .authenContent .autenHead img {
  width: 1.59375rem;
  height: 1.59375rem;
  margin: 0.375rem 0 0.3125rem 0;
}
.mine .authen .authenContent .autenHead > div {
  text-align: center;
}
.mine .authen .authenContent .autenHead > div p {
  font-size: 0.46875rem;
  color: #434343;
  font-family: 'simsun';
  font-weight: bold;
}
.mine .authen .authenContent .autenHead .autenHeadPng1 {
  width: 2.03125rem;
  float: left;
  margin-left: 1.71875rem;
}
.mine .authen .authenContent .autenHead .autenHeadPng2 {
  float: right;
  width: 2.5rem;
  margin-right: 1.71875rem;
}
.mine .authen .authenContent .authenFoot {
  width: 100%;
  height: 1.40625rem;
  line-height: 1.40625rem;
  margin-top: 0.1875rem;
  font-size: 0.46875rem;
  color: #a54be8;
  background: #ffffff;
  font-weight: bold;
  border-radius: 0.234375rem;
  text-align: center;
}



