
.circleDetails {
  width: 100%;
  height: 1.40625rem;
  background: #a54be8;
  /*circleLike*/
}
.circleDetails .circleDetailsHead {
  text-align: center;
  line-height: 1.40625rem;
  position: relative;
}
.circleDetails .circleDetailsHead img {
  left: 0.625rem;
  top: 0.46875rem;
  position: absolute;
  width: 0.3125rem;
  height: 0.546875rem;
}
.circleDetails .circleDetailsHead span {
  font-size: 0.5625rem;
  color: #fff;
}
.circleDetails .box {
  margin-top: 0.3125rem;
  position: relative;
}
.circleDetails .box div {
  display: inline-block;
}
.circleDetails .box .circleMes {
  position: absolute;
  right: 0.3125rem;
  bottom: 0.4375rem;
}
.circleDetails .box .circleMes img {
  width: 0.9375rem;
  height: 0.625rem;
}
.circleDetails .box .circleComment {
  position: absolute;
  bottom: 0.234375rem;
  right: 1.5625rem;
  background: #493d52;
  height: 1.09375rem;
  border-radius: 0.078125rem;
}
.circleDetails .box .circleComment .circleTread,
.circleDetails .box .circleComment .circleShare,
.circleDetails .box .circleComment .circleCom {
  height: 0.78125rem;
  margin: 0.15625rem;
  background: #6c597a;
  border-radius: 0.078125rem;
}
.circleDetails .box .circleComment img {
  width: 0.46875rem;
  height: 0.46875rem;
  vertical-align: middle;
  margin-top: -0.234375rem;
  margin-left: 0.1875rem;
}
.circleDetails .box .circleComment span {
  margin: 0 0.15625rem;
  font-size: 0.46875rem;
  line-height: 0.78125rem;
  color: #fff;
}
.circleDetails .box .content {
  height: 0;
}
.circleDetails .box .circleContent {
  /*height: 470/64rem;*/
  margin: 0 0 0.625rem 0.3125rem;
  position: relative;
}
.circleDetails .box .circleContent .circleShare {
  margin: 0.3125rem 0 0 7.34375rem;
}
.circleDetails .box .circleContent .circleShare img {
  width: 0.5625rem;
  height: 0.546875rem;
}
.circleDetails .box .circleContent .circleShare span {
  display: inline-block;
  font-size: 0.46875rem;
  color: #b676eb;
  margin: 0 0 0 0.25rem;
}
.circleDetails .box .circleContent .circleContentCar {
  z-index: 0;
  overflow: hidden;
  position: relative;
}
.circleDetails .box .circleContent .circleContentCar div {
  float: left;
}
.circleDetails .box .circleContent .circleContentCar .circlePortrait img {
  width: 0.9375rem;
  height: 0.9375rem;
}
.circleDetails .box .circleContent .circleContentCar .circleAuthor {
  font-size: 0.46875rem;
  color: #c79bef;
  margin-left: 0.234375rem;
}
.circleDetails .box .circleContent .circleContentCar .circleLike img {
  width: 0.78125rem;
  height: 0.78125rem;
  margin: 0.078125rem 0 0 4.84375rem;
}
.circleDetails .box .circleContent .circleContentCar .circleQuantity {
  font-size: 0.390625rem;
  color: #959595;
  line-height: 0.9375rem;
  margin-left: 0.125rem;
}
.circleDetails .box .circleContent .circleContentCar .circleTime,
.circleDetails .box .circleContent .circleContentCar .circleSite {
  position: absolute;
  top: 0.546875rem;
  font-size: 0.46875rem;
  color: #959595;
}
.circleDetails .box .circleContent .circleContentCar .circleTime {
  left: 1.171875rem;
}
.circleDetails .box .circleContent .circleContentCar .circleSite {
  left: 2.8125rem;
}
.circleDetails .box .circleContent .circleContentCar .circleContenter {
  width: 7.8125rem;
  margin-top: 0.3125rem;
  margin-left: 0.21875rem;
  font-size: 0.46875rem;
}
.circleDetails .box .circleContent .circleContentCar .circleSites {
  font-size: 0.46875rem;
  color: #959595;
  margin: 0.53125rem 0 0.3125rem 1.09375rem;
}
.circleDetails #circleMas {
  margin-left: 0rem;
}
.circleDetails .circleSplendid {
  position: relative;
  margin-left: 0.3125rem;
}
.circleDetails .circleSplendid .circleCharacter {
  font-size: 0.46875rem;
  border-left: 0.0625rem solid #A54BE8;
}
.circleDetails .circleSplendid .circleWire {
  position: absolute;
  display: inline-block;
  width: 70%;
  height: 0.015625rem;
  background: #d7d2e0;
  top: 0.234375rem;
  left: 2.1875rem;
}



