
.minePersonalData {
  width: 10rem;
  height: 100%;
  position: relative;
}
.minePersonalData .personalContent ul {
  margin: 0.46875rem 0 0.625rem 0;
  border-top: 0.015625rem solid #d7d2e0;
  border-bottom: 0.015625rem solid #d7d2e0;
  padding: 0 0.3125rem 0 0.46875rem;
  background: #ffffff;
}
.minePersonalData .personalContent ul li {
  border-bottom: 0.015625rem solid #d7d2e0;
  height: 1.40625rem;
  line-height: 1.40625rem;
  font-size: 0.5625rem;
  padding-left: 0.15625rem;
  color: #1B1B1B;
}
.minePersonalData .personalContent ul li i {
  display: block;
  width: 0.28125rem;
  height: 0.46875rem;
  float: right;
  margin-top: 0.5rem;
  margin-right: 0.15625rem;
  background: url('../../../assets/images/47/插入箭头.png') no-repeat center center;
}
.minePersonalData .personalContent ul li p {
  float: right;
  font-size: 0.4375rem;
  color: #707070;
  margin-right: 0.3125rem;
}
.minePersonalData .personalContent ul li:last-child {
  border: none;
}
.minePersonalData .personalContent .first li:first-child {
  height: 2.03125rem;
  line-height: 2.03125rem;
}
.minePersonalData .personalContent .first li:first-child i {
  margin-top: 0.78125rem;
}
.minePersonalData .personalContent .first li:first-child p {
  width: 1.25rem;
  height: 1.25rem;
  margin-top: 0.390625rem;
  border-radius: 50%;
  background: url('../../../assets/images/47/timg.jpg') no-repeat center center;
  background-size: 1.25rem 1.25rem;
}
.minePersonalData .personalContent .second li:last-child {
  height: 2.03125rem;
  line-height: 2.03125rem;
}
.minePersonalData .personalContent .second li:last-child i {
  margin-top: 0.78125rem;
}
.minePersonalData .personalContent .second li:last-child p {
  height: 1.03125rem;
  width: 4.875rem;
  line-height: 0.515625rem;
  margin-top: 0.5rem;
  overflow: hidden;
}
.minePersonalData .authen {
  width: 10rem;
  height: 100%;
  position: absolute;
  top: 1.5625rem;
  background: rgba(237, 237, 237, 0.4);
}
.minePersonalData .authen .authenContent {
  position: absolute;
  width: 9.375rem;
  margin: 0 0.3125rem;
  bottom: 0rem;
  z-index: 1000;
}
.minePersonalData .authen .authenContent .autenHead {
  width: 100%;
  height: 3.125rem;
  background: #FFFFFF;
  border-radius: 0.234375rem;
  text-align: center;
}
.minePersonalData .authen .authenContent .autenHead img {
  width: 1.59375rem;
  height: 1.59375rem;
  margin: 0.375rem 0;
}
.minePersonalData .authen .authenContent .autenHead > div {
  text-align: center;
  width: 1.59375rem;
}
.minePersonalData .authen .authenContent .autenHead > div p {
  font-size: 0.46875rem;
  color: #434343;
  font-family: 'simsun';
  font-weight: bold;
}
.minePersonalData .authen .authenContent .autenHead .autenHeadPng1 {
  float: left;
  margin-left: 1.71875rem;
}
.minePersonalData .authen .authenContent .autenHead .autenHeadPng2 {
  float: right;
  margin-right: 1.71875rem;
}
.minePersonalData .authen .authenContent .authenFoot {
  width: 100%;
  height: 1.40625rem;
  line-height: 1.40625rem;
  margin-top: 0.1875rem;
  font-size: 0.46875rem;
  color: #a54be8;
  background: #ffffff;
  font-weight: bold;
  border-radius: 0.234375rem;
  text-align: center;
}



