
.circleMaster .circleMasterHead {
  width: 100%;
  height: 1.40625rem;
  text-align: center;
  line-height: 1.40625rem;
  position: relative;
  background: #a54be8;
}
.circleMaster .circleMasterHead img {
  left: 0.625rem;
  top: 0.46875rem;
  position: absolute;
  width: 0.3125rem;
  height: 0.546875rem;
}
.circleMaster .circleMasterHead span {
  font-size: 0.5625rem;
  color: #fff;
}
.circleMaster .circleMasterCon {
  margin: 0.3125rem 0 0.15625rem 0.3125rem;
  overflow: hidden;
}
.circleMaster .circleMasterCon .circleMasterCon:before {
  position: absolute;
  content: "";
  width: 80%;
  height: 0.15625rem;
  background: #d7d2e0;
  bottom: -0.3125rem;
}
.circleMaster .circleMasterCon .circleMasterImg,
.circleMaster .circleMasterCon .circleMasterTen {
  float: left;
}
.circleMaster .circleMasterCon .circleMasterImg img {
  height: 2.34375rem;
}
.circleMaster .circleMasterCon .circleMasterTen {
  width: 5.625rem;
  position: relative;
}
.circleMaster .circleMasterCon .circleMasterTen h1 {
  font-size: 0.53125rem;
  font-weight: bold;
  margin: 0 0 0.15625rem 0.3125rem;
}
.circleMaster .circleMasterCon .circleMasterTen .circleMasterCharacter {
  margin: 0 0 0 0.3125rem;
}
.circleMaster .circleMasterCon .circleMasterTen .circleMasterCharacter span {
  font-size: 0.4375rem;
  color: #959595;
}
.circleMaster .circleMasterCon .circleMasterWire {
  width: 80%;
  height: 0.015625rem;
  /*background: #d7d2e0;*/
  background: red;
  /*margin: 50/64rem 0;*/
}
.circleMaster .footer .circleMasterBox {
  width: 2.34375rem;
  margin: 0 auto;
  margin-top: 3.59375rem;
}
.circleMaster .footer .circleMasterBox .circleMasterIcon,
.circleMaster .footer .circleMasterBox .circleMasterWz {
  display: inline-block;
}
.circleMaster .footer .circleMasterBox .circleMasterWz {
  font-size: 0.25rem;
  margin-left: 0.21875rem;
}
.circleMaster .footer .circleMasterBox .circleMasterIcon img {
  vertical-align: middle;
}



