
* {
  margin: 0;
  padding: 0;
}
.circleIndex {
  margin-bottom: 1.71875rem;
}
.circleIndex .circleIndexHead {
  background: #a54be8;
  width: 100%;
  height: 1.40625rem;
}
.circleIndex .circleIndexHead .circleIndexHeadCon {
  position: relative;
  text-align: center;
}
.circleIndex .circleIndexHead .circleIndexHeadCon .circleCharacter {
  font-size: 0.5625rem;
  line-height: 1.40625rem;
  color: #fff;
}
.circleIndex .circleIndexHead .circleIndexHeadCon .more {
  position: absolute;
  z-index: 100;
  width: 100%;
  height: 1.33333333rem;
  background: rgba(165, 75, 232, 0.7);
}
.circleIndex .circleIndexHead .circleIndexHeadCon .more ul {
  overflow: hidden;
}
.circleIndex .circleIndexHead .circleIndexHeadCon .more ul li {
  font-size: 0.4rem;
  display: inline-block;
  margin-top: 0.26666667rem;
  line-height: 0.8rem;
  width: 1.73333333rem;
  height: 0.8rem;
  color: #fff;
  border-radius: 0.66666667rem;
}
.circleIndex .circleIndexHead .circleIndexHeadCon .more ul .frist {
  background: rgba(145, 69, 211, 0.7);
}
.circleIndex .circleIndexHead .circleIndexHeadCon .circleImg {
  width: 0.36rem;
  height: 0.36rem;
}
.circleIndex .circleIndexHead .circleIndexHeadCon img {
  width: 0.48rem;
  height: 0.49333333rem;
}
.circleIndex .circleIndexHead .circleIndexHeadCon .circleIndexIcon {
  position: absolute;
  bottom: 36%;
  right: 0.4rem;
  /*margin-left: 100/75rem;*/
}
.circleIndex .box {
  margin-top: 0.4375rem;
  position: relative;
}
.circleIndex .box div {
  display: inline-block;
}
.circleIndex .box .circleMes {
  /*position: absolute;*/
  margin: -0.13333333rem 0 0 8.66666667rem;
  right: 0.26666667rem;
  bottom: 0.37333333rem;
}
.circleIndex .box .circleMes img {
  width: 0.8rem;
  height: 0.53333333rem;
}
.circleIndex .box .circleComment {
  position: absolute;
  bottom: 0rem;
  right: 1.33333333rem;
  /*display: inline-block;*/
  /*margin: -100/75rem 0 0 150/75rem;*/
  /*margin-top: -1100/75rem;*/
  background: #493d52;
  height: 0.93333333rem;
  border-radius: 0.06666667rem;
}
.circleIndex .box .circleComment .circleTread,
.circleIndex .box .circleComment .circleShare,
.circleIndex .box .circleComment .circleCom {
  height: 0.66666667rem;
  margin: 0.13333333rem;
  background: #6c597a;
  border-radius: 0.06666667rem;
}
.circleIndex .box .circleComment img {
  width: 0.4rem;
  height: 0.4rem;
  vertical-align: middle;
  margin-top: -0.2rem;
  margin-left: 0.16rem;
}
.circleIndex .box .circleComment span {
  margin: 0 0.13333333rem;
  font-size: 0.4rem;
  line-height: 0.66666667rem;
  color: #fff;
}
.circleIndex .box .circleContent {
  /*height: 470/75rem;*/
  margin: 0 0 0.53333333rem 0.3125rem;
}
.circleIndex .box .circleContent .circleContentCar {
  z-index: 0;
  overflow: hidden;
  position: relative;
}
.circleIndex .box .circleContent .circleContentCar div {
  float: left;
}
.circleIndex .box .circleContent .circleContentCar .circlePortrait img {
  width: 0.93333333rem;
  height: 0.93333333rem;
}
.circleIndex .box .circleContent .circleContentCar .circleAuthor {
  font-size: 0.46875rem;
  color: #c79bef;
  margin-left: 0.2rem;
}
.circleIndex .box .circleContent .circleContentCar .circleLike img {
  width: 0.66666667rem;
  height: 0.66666667rem;
  margin: 0.06666667rem 0 0 5.46666667rem;
}
.circleIndex .box .circleContent .circleContentCar .circleQuantity {
  font-size: 0.390625rem;
  color: #959595;
  line-height: 0.8rem;
  margin-left: 0.10666667rem;
}
.circleIndex .box .circleContent .circleContentCar .circleTime,
.circleIndex .box .circleContent .circleContentCar .circleSite {
  position: absolute;
  top: 0.46666667rem;
  font-size: 0.46666667rem;
  color: #959595;
}
.circleIndex .box .circleContent .circleContentCar .circleTime {
  left: 1.13333333rem;
}
.circleIndex .box .circleContent .circleContentCar .circleSite {
  left: 2.4rem;
}
.circleIndex .box .circleContent .circleContentCar .circleContenter {
  width: 8rem;
  margin-top: 0.26666667rem;
  font-size: 0.46666667rem;
  margin-left: 1.13333333rem;
}
.circleIndex .box .circleContent .circleContentCar .Img {
  margin-top: 0.26666667rem;
  margin-left: 1.13333333rem;
}



