
.jdIndex_footer {
  position: fixed;
  bottom: 0;
}
.jdIndex_footer ul {
  height: 1.33333333rem;
  background-color: #f3f3f3;
  opacity: 0.9;
  filter: alpha(opacity=40);
  /* 针对 IE8 以及更早的版本 */
}
.jdIndex_footer ul li {
  width: 2.46666667rem;
  /*border: 1px solid saddlebrown;*/
  float: right;
  opacity: 0.2;
  filter: alpha(opacity=20);
  margin-top: 0.24rem;
}
.dIndex_footer_nav div {
  margin-left: 0.88rem;
  font-size: 0.29333333rem;
}
.jdIndex_footer {
  color: #d5c0e0;
}
.jdIndex_footer .active {
  opacity: 1;
  filter: alpha(opacity=1);
  color: #a54be8;
}
.dIndex_footer_nav img {
  height: 0.53333333rem;
  width: 0.53333333rem;
  margin-bottom: 0.09333333rem;
}
