/*轮播图开始*/
.swiper-container {
  width: 100%;
  height: 10rem;
}
.swiper-container .swiper-wrapper {
  width: 100%;
  height: 100%;
}
.swiper-container .swiper-slide {
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 100%;
}
.swiper-container .swiper-slide img {
  width: 100%;
  height: 100%;
}
.swiper-container .swiper-pagination-bullet {
  width: 0.233rem;
  height: 0.233rem;
  display: inline-block;
  background: #7c5e53;
}
/*轮播图结束*/
.jdIndex_body_box {
  width: 10rem;
  height: 100%;
  padding-bottom: 1.33333333rem;
  position: relative;
}
.jdIndex_body {
  margin-top: 1.2rem;
  height: 100%;
  background-color: #f2eef9;
  /*position: absolute;*/
}
.jdIndex_body_box {
  /*position: absolute;*/
}
.clearfix:after {
  height: 0;
  content: " ";
  display: block;
  overflow: hidden;
  clear: both;
}
.clearfix {
  zoom: 1;
  /*IE低版本浏览器不支持after伪类所以要加这一句*/
}
.jdIndex_body1 {
  width: 10rem;
  height: 4.10666667rem;
}
.jdIndex_body_xq_img img {
  width: 4.38666667rem;
  margin-left: 0.4rem;
}
.jdIndex_body_box {
  margin-top: 0.4rem;
}
.jdIndex_body_xq {
  margin-bottom: 0.4rem;
}
.jdIndex_body_name {
  font-size: 0.36rem;
  color: black;
  margin-top: 0.06666667rem;
  margin-left: 0.4rem;
}
.jdIndex_body_jj {
  font-size: 0.32rem;
  color: #9c9c9d;
  margin-top: 0.06666667rem;
  margin-left: 0.4rem;
}
.jdIndex_body_box .jdIndex_body_xq {
  float: left;
}



