
.y_ping {
  font-size: 0.48rem;
  height: 1.33333333rem;
  text-align: center;
  line-height: 1.33333333rem;
  background: #A54BE8;
  color: #fff;
  overflow: hidden;
  position: relative;
}
.y_ping .img1 {
  left: 0.625rem;
  top: 0.390625rem;
  position: absolute;
  width: 0.3125rem;
  height: 0.546875rem;
}
.y_ping .img2 {
  margin-left: 8.26666667rem;
  width: 0.3125rem;
  height: 0.546875rem;
}
.y_field {
  min-height: 2rem;
}
.y_toupho {
  width: 1.06666667rem;
  height: 1.06666667rem;
  float: left;
  border: 1px solid #0097FF;
  border-radius: 50px;
  margin-top: 0.2rem;
  margin-left: 0.26666667rem;
  margin-right: 0.21333333rem;
  /*margin-bottom: 70/75rem;*/
}
.y_back {
  background: url("../../../assets/images/42-43/d1.png") no-repeat;
  margin-top: 0.4rem;
  margin-left: 1.6rem;
  margin-right: 1.92rem;
  min-height: 0.66666667rem;
  position: relative;
}
.y_back P {
  position: absolute;
  left: 0.26666667rem;
  top: 0.13333333rem;
  right: 0.13333333rem;
  font-size: 0.42666667rem;
  color: #626262s;
}
.y_ying {
  overflow: hidden;
  height: 1.33333333rem;
}
.ying_left,
.ying_right {
  float: left;
  line-height: 1.33333333rem;
  width: 50%;
  background: #A54BE8;
  text-align: center;
}
.ying_left span,
.ying_right span {
  display: inline-block;
  line-height: 1.33333333rem;
  color: #FFFFFF;
  font-size: 0.42666667rem;
}



