
.jdFlower_box {
  /*overflow: hidden;*/
  width: 10rem;
  height: 18rem;
  position: relative;
}
#jdFlower_box_left {
  /*display: none;*/
  width: 4rem;
  height: 100%;
  background-image: url(../../assets/images/2/menu_bg.png);
  background-size: 4rem 100%;
  font-size: 100px;
  text-align: center;
  z-index: 11000;
  position: absolute;
  top: 0;
  left: -4rem;
}
#jdFlower_box_right {
  overflow: hidden;
  width: 10rem;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  margin-bottom: 1.33333333rem;
  /*float: left;*/
}
.jdFlower_box_left_logo {
  height: 1.33333333rem;
  width: 2.45333333rem;
  margin-top: 1.06666667rem;
}
.jdFlower_box_left_sign {
  height: 0.66666667rem;
  width: 1.73333333rem;
}
.jdFlower_box_left ul {
  margin-top: 0.4rem;
}
.jdFlower_box_left ul li {
  margin-bottom: 0rem;
}
/*菜单按钮显示隐藏开始*/
/*菜单按钮显示隐藏结束*/
/*头部样式开始*/
#jdFlower_box_right .header {
  position: fixed;
  top: 0;
  z-index: 10000;
}
.jdIndex_box {
  width: 10rem;
  height: 17.78666667rem;
}
.jdIndex_head {
  width: 10rem;
  height: 1.2rem;
  background: #a54be8;
}
.jdIndex_head_menu,
.jdIndex_head_hotebang,
.jdIndex_head_big {
  float: left;
}
.jdIndex_head_menu_img {
  height: 0.36rem;
  width: 0.53333333rem;
  margin-left: 0.4rem;
  margin-top: 0.37333333rem;
  z-index: 5000;
}
.jdIndex_head_hotebang {
  color: white;
  font-size: 0.48rem;
  margin-top: 0.37333333rem;
  margin-left: 3.58666667rem;
}
.jdIndex_head_big_img {
  height: 0.49333333rem;
  width: 0.49333333rem;
  margin-top: 0.37333333rem;
  margin-left: 3.58666667rem;
}
/*头部样式结束*/
/*登陆样式开始*/
.jdFlower_box_Login {
  height: 6.18666667rem;
  width: 8.2rem;
  background: white;
  position: absolute;
  left: 0.86666667rem;
  top: 5.2rem;
  text-align: center;
  /*z-index: 0;*/
  border-radius: 15px;
  display: none;
  z-index: 100000;
}
.jdFlower_box_Login_title {
  margin-top: 0.45333333rem;
  color: #434343;
  font-size: 0.4rem;
  margin-bottom: 0.45333333rem;
}
.jdFlower_box_Sigbtn,
.jdFlower_box_Logbtn {
  height: 1.2rem;
  width: 3.46666667rem;
  float: left;
  margin-left: 0.4rem;
  text-align: center;
}
.jdFlower_box_Sigbtn span,
.jdFlower_box_Logbtn span {
  font-size: 0.53333333rem;
  font-weight: 400;
  line-height: 1.2rem;
}
.jdFlower_box_Sigbtn {
  background-image: url(../../assets/images/3/dialog_login_warm_reg_btn_normal.png);
  background-size: 3.46666667rem 1.2rem;
  color: white;
}
.jdFlower_box_Logbtn {
  background-image: url(../../assets/images/3/dialog_login_warm_login_btn_normal.png);
  background-size: 3.46666667rem 1.2rem;
  color: #a54be8;
}
.jdFlower_box_Logbtn_hint {
  color: #a9a2b4;
  font-size: 0.4rem;
  display: inline-block;
  margin-top: 0.8rem;
  margin-bottom: 0.4rem;
}
.jdFlower_box_Login_signbox {
  width: 7.46666667rem;
  /*border: 1px solid saddlebrown;*/
  margin-left: 0.26666667rem;
}
.jdFlower_box_Login_wx,
.jdFlower_box_Login_qq,
.jdFlower_box_Login_wb {
  float: left;
  height: 1.33333333rem;
  width: 1.33333333rem;
  margin: 0 0.57333333rem;
}
.jdFlower_box_Login_wx {
  background-image: url(../../assets/images/3/a微信-1.png);
  background-size: 1.33333333rem 1.33333333rem;
}
.jdFlower_box_Login_qq {
  background-image: url(../../assets/images/3/a朋友圈-1.png);
  background-size: 1.33333333rem 1.33333333rem;
}
.jdFlower_box_Login_wb {
  background-image: url(../../assets/images/3/a微博-1.png);
  background-size: 1.33333333rem 1.33333333rem;
}
.jdFlower_box_left_siginState {
  display: block;
}
/*登陆样式结束*/



