
.mineSetBtn {
  width: 10rem;
}
.mineSetBtn .newMessage {
  position: relative;
  height: 1.40625rem;
  line-height: 1.40625rem;
  background: #ffffff;
  margin-top: 0.46875rem;
  border-bottom: 0.015625rem solid #d7d2e0;
  border-top: 0.015625rem solid #d7d2e0;
}
.mineSetBtn .newMessage p {
  font-size: 0.5625rem;
  color: #1B1B1B;
  padding-left: 0.46875rem;
}
.mineSetBtn .newMessage .switch {
  position: absolute;
  top: 0.3125rem;
  right: 0.46875rem;
  width: 1.5625rem;
  height: 0.78125rem;
  background: #ccc;
  border-radius: 0.78125rem;
}
.mineSetBtn .newMessage .switch:before {
  position: absolute;
  top: 0.0625rem;
  left: 0.0625rem;
  content: '';
  width: 0.65625rem;
  height: 0.65625rem;
  border-radius: 50%;
  background: white;
  -webkit-transition: 0.3s ease-in;
  transition: 0.3s ease-in;
}
.mineSetBtn .newMessage .switch.checked {
  background: #a54be8;
}
.mineSetBtn .newMessage .switch.checked:before {
  left: calc(54%);
}
.mineSetBtn .changeLi ul {
  padding-left: 0.46875rem;
  padding-right: 0.3125rem;
  margin-top: 0.625rem;
  background: #ffffff;
  font-size: 0.5625rem;
  color: #1B1B1B;
  border-bottom: 0.015625rem solid #d7d2e0;
  border-top: 0.015625rem solid #d7d2e0;
}
.mineSetBtn .changeLi ul li {
  line-height: 1.40625rem;
  border-bottom: 0.015625rem solid #d7d2e0;
  font-family: "微软雅黑";
}
.mineSetBtn .changeLi ul li span {
  display: block;
  width: 0.28125rem;
  height: 0.46875rem;
  float: right;
  margin-top: 0.5rem;
  margin-right: 0.5rem;
  background: url('../../../assets/images/47/插入箭头.png') no-repeat center center;
}
.mineSetBtn .changeLi ul li:last-child {
  border: none;
}
.mineSetBtn .out {
  margin-top: 7.03125rem;
  height: 1.40625rem;
  line-height: 1.40625rem;
  background: #ffffff;
  text-align: center;
  font-size: 0.5625rem;
  color: #e43c3c;
  border-bottom: 0.015625rem solid #d7d2e0;
  border-top: 0.015625rem solid #d7d2e0;
}
.el-switch.is-checked .el-switch__core {
  border-color: #a54be8;
  background-color: #a54be8;
}



